import axios from "axios";

const SET_CHARACTERS = "characters/SET_CHARACTERS";
const SET_EPISODES = "episodes/SET_EPISODES";
const SET_LOCATIONS = "locations/SET_LOCATIONS";
const SET_REQUESTINFO = "requestInfo/SET_REQUESTINFO";

let initialState = {
  characters: [],
  episodes: [],
  locations: [],
  requestInfo: {},
};
export const reducer = (state = initialState, action) => {
  switch (action.type) {
    case SET_CHARACTERS:
      return {
        ...state,
        characters: action.payload,
      };
    case SET_EPISODES:
      return {
        ...state,
        episodes: action.payload,
      };
    case SET_LOCATIONS:
      return {
        ...state,
        locations: action.payload,
      };
    case SET_REQUESTINFO:
      return {
        ...state,
        requestInfo: action.payload,
      };
    default:
      return state;
  }
};

const setCharacters = (items) => ({
  type: SET_CHARACTERS,
  payload: items,
});
const setRequestInfo = (items) => ({
  type: SET_REQUESTINFO,
  payload: items,
});
const setEpisodes = (items) => ({
  type: SET_EPISODES,
  payload: items,
});
const setLocations = (items) => ({
  type: SET_LOCATIONS,
  payload: items,
});

export const fetchCharacters =
  (active, species, status, gender) => (dispatch) => {
    console.log("species", species, "status", status, "gender", gender);
    let url = `https://rickandmortyapi.com/api/character?page=${active}`;
    if (species)
      url = `https://rickandmortyapi.com/api/character/?page=${active}&species=${species}`;
    if (status)
      url = `https://rickandmortyapi.com/api/character/?page=${active}&status=${status}`;
    if (gender)
      url = `https://rickandmortyapi.com/api/character/?page=${active}&gender=${gender}`;
    if (species && status)
      url = `https://rickandmortyapi.com/api/character/?page=${active}&species=${species}&status=${status}`;
    if (species && gender)
      url = `https://rickandmortyapi.com/api/character/?page=${active}&species=${species}&gender=${gender}`;
    if (status && gender)
      url = `https://rickandmortyapi.com/api/character/?page=${active}&status=${status}&gender=${gender}`;
    if (species && status && gender)
      url = `https://rickandmortyapi.com/api/character/?page=${active}&species=${species}&status=${status}&gender=${gender}`;
    return axios
      .get(url)
      .then((resp) => {
        dispatch(setCharacters(resp.data.results));
        dispatch(setRequestInfo(resp.data.info));
      })
      .catch((err) => console.error(err));
  };

export const fetchEpisodes = (active) => (dispatch) => {
  return axios
    .get(`https://rickandmortyapi.com/api/episode?page=${active}`)
    .then((resp) => {
      dispatch(setEpisodes(resp.data.results));
      dispatch(setRequestInfo(resp.data.info));
    })
    .catch((err) => console.error(err));
};

export const fetchLocations = (active) => (dispatch) => {
  return axios
    .get(`https://rickandmortyapi.com/api/location?page=${active}`)
    .then((resp) => {
      dispatch(setLocations(resp.data.results));
      dispatch(setRequestInfo(resp.data.info));
    })
    .catch((err) => console.error(err));
};
